function configureProxy($scope,$uibModal){
     $scope.getProxySettings(function(){
         $scope.cancelText="Cancel";
         $scope.isInsideModal = true;

         $scope.skipSettings = function(){
             networkSettingsModal.dismiss();
         };

         $scope.doneSuccessCallback = function(){
             networkSettingsModal.dismiss();
         };

         console.log("show network settings from dashboard");
         var networkSettingsModal = $uibModal.open({
             templateUrl: '/partials/hub.networksettings.dashboard.ng.html?version=' + gui_version,
             animation: true,
             size: 'lg',
             scope: $scope,
             backdrop: modal_light_dismiss_enabled
         });

         $scope.closeModal = function(){
             console.log("hub canceling os upgrade #9033b");
             networkSettingsModal.dismiss('cancel');
         };
     });
 }