iotgatewayhub.directive( 'dynamicContentLoad', function() {
    return {
        restrict: 'A',
        template: '<ng-include src="getTemplateUrl()"/>',
        link: function(scope, element, attr) {
            scope.$watch('dynamicChangeViewPartial', function(data) {
                scope.getTemplateUrl = function () {
                    return data ? data : null;
                };
            }, true);
        }
    };
});
