function enableHttps($scope,$uibModal){
    var modalInstance = $uibModal.open({
        templateUrl: 'partials/hub.confirm.toggle.security.ng.html?version=' + gui_version,
        animation:    true,
        size: 'lg',
        backdrop: modal_light_dismiss_enabled,
        scope: $scope,
        controller: function($scope,$state,$http,$rootScope){
            $scope.toggled_security = false;

            $scope.closeModal = function(){
                modalInstance.dismiss('cancel');
            };

            $scope.toggleSecurity = function(){
                if($scope.toggling_security || $scope.toggled_security){
                    return;
                }

                $scope.toggling_security = true;

                var _enable = 'true';
                if($scope.security_enabled){
                    _enable = 'false';
                }

                // briefly cancel the network checking so that no connection error won't pop-up.
                // When we go to state.hub again, it will be rescheduled.
                $scope.stopCheckingNetwork();
                tempURL = '/api/sa?enable=' + _enable;
                workerProcessRestService.performGeneralExecution('PUT', tempURL, false, false, null,
                    function successCallback(data) {
                        $scope.toggling_security = false;
                        $scope.toggled_security = true;
                        setTimeout(function(){
                            if(!$scope.security_enabled){
                                window.location = 'https://'+window.location.hostname+":"+https_port;
                            }else{
                                window.location = 'http://'+window.location.hostname;
                            }
                        },toggle_security_timeout_milliseconds);
                    }, function errorCallback(errorMessage) {
                        $scope.toggling_security = false;
                        $scope.error_message = errorMessage;
                        $scope.startCheckingNetwork();
                    }, rest_check_interval_milliseconds);
            };
        }
    });
}