function changePassword($scope) {
    $scope.update = {
        username: null,
        password: null,
        newpassword: null,
        confirmnewpassword: null
    };

    $scope.changePassword = function(){

        $scope.error_message = null; //reset so error message goes away
        console.log("hub changing password #2029a");

        var _username           = $scope.update.username;
        var _password           = $scope.update.password;
        var _newpassword        = $scope.update.newpassword;
        var _confirmnewpassword = $scope.update.confirmnewpassword;

        if(!_newpassword || _newpassword.length<3){
            $scope.error_message = "The new password must be at least 3 characters long.";
        }else if(_newpassword!==_confirmnewpassword){
            $scope.error_message = "The new passwords do not match.";
        }else{
            $http({
                method: 'PUT',
                url:    '/api/auth',
                data:  JSON.stringify({"username":_username,"password":_password,"newpassword":_newpassword}),
                headers : {
                    'Content-type' : 'application/json'
                }
            }).then(function successCallback(response) {

            if(response.data.status==='success'){
                console.log("hub change password succeded #2029b");
                $scope.openConfirmChangePasswordModal(_username);
                modalInstance.dismiss('cancel');
            }else{
                console.log("hub change password failed #2029F");
                $scope.error_message = "ERROR: "+response.data.error;
                //what to do if fails?
                }
            }, function errorCallback(response) {
                console.log("hub change password failed #2029F");
                $scope.error_message = "ERROR: "+response;

            });
        }

    };
}