iotgatewayhub.config(function($stateProvider,$urlRouterProvider){

    $stateProvider.state('hub.dashboard.packages',{
        url: '/dashboard/packages',
        templateUrl: '/partials/hub.dashboard.packages.ng.html?version=' + gui_version,
        controller: function($scope,$state,$http,$rootScope,$uibModal){
            var num_to_show = 100;
            $scope.gettingPackages = true;
            $scope.showLoadMore = false;
            $scope.sortType = 'name';
            $scope.totalDisplayed = num_to_show;

            $scope.loadMore = function() {
                $scope.totalDisplayed += num_to_show;
                if ($scope.totalDisplayed >= $scope.installed.length) {
                    $scope.showLoadMore = false;
                }
            };
            if ($scope.device.package_config == "enabled"){
                if ($scope.connected){
                    $scope.setPackages('True','',
                    function successCallback(data){
                        $scope.gettingPackages = false;

                        if ($scope.installed.length <= $scope.totalDisplayed) {
                            $scope.showLoadMore = false;
                        } else {
                            $scope.showLoadMore = true;
                        }
                    }, function errorCallback(response) {
                        $scope.gettingPackages = false;
                    });
                }
                else{
                    $scope.setPackages('False','',
                    function successCallback(data){
                        $scope.gettingPackages = false;

                        if ($scope.installed.length <= $scope.totalDisplayed) {
                            $scope.showLoadMore = false;
                        } else {
                            $scope.showLoadMore = true;
                        }
                    }, function errorCallback(response) {
                        $scope.gettingPackages = false;
                    });
                }
            }

        }
    })
    .state('hub.dashboard.package',{
        url: '/package',
        templateUrl: '/partials/hub.package.ng.html?version=' + gui_version,
        params: { packageUrl : null, packageName:null},
        controller: function($window,$scope,$state,$rootScope,$stateParams){
            console.log("launching package at url:"+$stateParams.packageUrl);
            $scope.packageUrl = $stateParams.packageUrl;
            $scope.packageName =  $stateParams.packageName;
            if(!$scope.packageUrl){
                $state.go("hub.dashboard.packages");
            }
        }
    });
});
