// These are general functions used in several states.
iotgatewayhub.expandHub_save_image = function($scope,$http,$uibModal,workerProcessRestService) {
    ///////////////////////////////////////
    // Save Image Functions
    //////////////////////////////////////

    $scope.getDefaultConfigurationForSaveImage = function(passedSuccessCallback, passedErrorCallback){
        $http({
            method : 'GET',
            url    : '/api/sa?type=save_image_custom&nocache=' + new Date().getTime()
        }).then(
            function successCallback(response) {
                if(_.isFunction(passedSuccessCallback)){ passedSuccessCallback(response.data); }
            },function errorCallback(response) {
                if(_.isFunction(passedErrorCallback)){ passedErrorCallback(response); }
            });
    };

    ///////////////////////////////////////
    // GUI Modals
    //////////////////////////////////////

    $scope.openOSImageModal0 = function(){
        if(!$scope.mec){
            console.log("hub control NOT opening OS IMAGE modal because no MEC available #038X");
            return;
        }
        console.log("hub control opening OS IMAGE modal #038a");
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.osimage.0.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope){
                console.log("hub control opening OS image modal 0 #038b");
                $scope.closeOSImageModal = function(){
                    console.log("hub control closing os image modal 0 #038c");
                    modalInstance.dismiss('cancel');
                };

                $scope.continueOSImage1 = function(){
                    console.log("continuing to os image modal 1 #038dx");
                    $scope.openOSImageModal1();
                    modalInstance.dismiss('cancel');
                };
            }
        });
    };

    $scope.openOSImageModal1 = function(){
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.osimage.1.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope){
                //$scope.scanUSBDevices();
                $scope.selected_device   = null;
                $scope.closeOSImageModal = function(){
                    modalInstance.dismiss('cancel');
                };
                $scope.setSelectedDevice = function(_device){
                    console.log("setting usb device as "+_device.Name);
                    $scope.selected_device   = _device;
                };
                $scope.continueOSImage2 = function(){
                    if($scope.selected_device!==null){
                        $scope.openOSImageModal2($scope.selected_device);
                        modalInstance.dismiss('cancel');
                    }
                };
            }
        });
    };

    $scope.openOSImageModal2 = function(_usb_device){
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.osimage.2.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope){
                $scope.selected_device   = _usb_device;
                $scope.closeOSImageModal2 = function(){
                    modalInstance.dismiss('cancel');
                };

                $scope.continueOSImage3 = function(){
                    $scope.openOSImageModal3(_usb_device, false);
                    modalInstance.dismiss('cancel');
                };
            }
        });
    };

    $scope.openOSImageModal3 = function(_usb_device, from_custom){
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.osimage.3.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope){

                var update_params = {
                    'harden_type'                : 'standard',
                    'usb_device'                 : _usb_device,
                    'packages_removed'           : [],
                    'updaters'                   : [],
                    'users'                      : [],
                    'stig'                       : [],
                    'new_admin_password'         : null,
                    'confirm_admin_password'     : null,
                    'new_mec_admin_password'     : null,
                    'confirm_mec_admin_password' : null,
                    'enable_mec_whitelist'       : true,

                };

                $scope.parseVersion = function(){
                    var sysVersionSplit = $scope.device.system_version.split(".");
                    var rcplVersion = sysVersionSplit[3];
                    console.log(rcplVersion);
                    if (rcplVersion < 18) {
                        console.log("VERSION < 18");
                        $scope.rcplLessThanEighteen = true;
                    }
                    else {
                        console.log("VERSION >= 18");
                        $scope.rcplLessThanEighteen = false;
                    }
                    return $scope.rcplLessThanEighteen;
                };

                $scope.selection = {
                    type: 'standard'
                };
                if (from_custom) {
                    $scope.selection.type = 'custom';
                }
                $scope.closeOSImageModal3 = function(){
                    modalInstance.dismiss('cancel');
                };
                $scope.continueOSImage3 = function(){
                    if ($scope.selection.type === 'standard') {
                        $scope.openOSImageModal3_Standard(update_params);
                    } else {
                        var my_data = {
                            config_data: {},
                            install_list: [],
                            updaters_list: null,
                            users_list: null,
                            stig_list: null
                        };
                        $scope.openOSImageModal3_Custom_Packages(update_params, my_data);
                    }
                    modalInstance.dismiss('cancel');
                };
            }

        });
    };

    $scope.openOSImageModal3_Standard = function(_update_params){
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.osimage.3.standard.pw.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope){
                $scope.selected_device   = _update_params.usb_device;
                $scope.error_message     = null;
                $scope.hasMEC            = function(){
                    return $scope.mec.status==='success';
                };
                $scope.update_params = _update_params;
                $scope.update_params.harden_type = 'standard';
                $scope.closeOSImageModal3_Standard = function(){
                    modalInstance.dismiss('cancel');
                };
                $scope.continueOSImage4_FromStandard = function(){
                    $scope.error_message     = null;
                    console.log("hub osimage4 attempting os image with password set ["+$scope.update_params.new_admin_password+","+$scope.update_params.confirm_admin_password+";"+$scope.update_params.new_mec_admin_password+","+$scope.update_params.confirm_mec_admin_password+"]");
                    if(!$scope.update_params.new_admin_password || $scope.update_params.new_admin_password.length < min_password_length){
                        $scope.error_message     = "The admin password must be at least "+min_password_length+" characters long.";
                        return;
                    }
                    if($scope.update_params.new_admin_password !==  $scope.update_params.confirm_admin_password){
                        console.log("new admin params do not match");
                        $scope.error_message     = "The admin passwords do not match";
                        return;
                    }
                    if(!$scope.update_params.new_mec_admin_password || $scope.update_params.new_mec_admin_password.length < min_password_length){
                        $scope.error_message     = "The MEC admin password must be at least "+min_password_length+" characters long.";
                        return;
                    }
                    if($scope.update_params.new_mec_admin_password !==  $scope.update_params.confirm_mec_admin_password){
                        console.log("new MEC admin params do not match");
                        $scope.error_message     = "The MEC admin passwords do not match";
                        return;
                    }
                    $scope.openOSImageModal4($scope.update_params);
                    modalInstance.dismiss('cancel');
                };
            }

        });
    };

    $scope.openOSImageModal3_Custom_Packages = function(_update_params, _data){
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.osimage.3.custom.packages.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope){
                $scope.sortType = 'name';
                $scope.save_image_has_error = false;
                $scope.save_image_error_message = '';
                $scope.gettingPackages = true;
                $scope.custom_configuration_data = _data.config_data;

                // filter for ng-repeat to show to-be-removed entry
                $scope.search_removed = {'remove': true};
                // filter for ng-repeat to show selected packages
                $scope.search_packages = {'name': null};

                // watch for search condition and reset the scroll bar to top when the search condition changes.
                $scope.$watch('search_packages', function(){
                    var container = document.getElementById('my_scroll_bar');
                    container.scrollTop = 0;
                }, true);

                // create my array for packages
                $scope.custom_installed_list = _data.install_list;
                // if the list is empty, init the list
                if ($scope.custom_installed_list.length === 0) {
                    $scope.getDefaultConfigurationForSaveImage(
                        function successCallback(data){
                            $scope.custom_configuration_data = data;
                            $scope.getPackagesInstalled(
                                 function successCallback(data){
                                    if (data) {
                                        $scope.processData(data);
                                    } else {
                                        $scope.showError('The server is still processing the packages list. Please try again later.');
                                    }
                                 }, function errorCallback(response){
                                    $scope.showError('Failed to get installed packages list! HTTP status is ' + response.status);
                                 });
                        }, function errorCallback(response) {
                            $scope.showError('Failed to get default configuration! HTTP status is ' + response.status);
                        });
                } else {
                    $scope.gettingPackages = false;
                }

                // for each installed package, if it is in the list of default to-be-removed list,
                //      mark it as to-be-removed in $scope.custom_installed_list.
                // $scope.custom_installed_list is bind to angular html
                $scope.processData = function(data) {
                    $scope.gettingPackages = false;
                    for (var key in data) {
                        var bEnforce = false;
                        var bRemove = false;
                        var lowerName = key.toLowerCase();
                        var remove_dict = {};
                        if (typeof $scope.custom_configuration_data.packages_removed != 'undefined') {
                            remove_dict = $scope.custom_configuration_data.packages_removed;
                        }

                        for (var key2 in remove_dict) {
                            var value2 = remove_dict[key2];
                            if (lowerName === key2.toLowerCase()) {
                                bRemove = true;
                                bEnforce = value2;
                            }
                        }

                        $scope.custom_installed_list.push({
                            "name": key,
                            "remove": bRemove,
                            "enforce": bEnforce
                        });
                    }
                };

                $scope.showError = function(err_message) {
                    $scope.gettingPackages = false;
                    $scope.save_image_has_error = true;
                    $scope.save_image_error_message = err_message;
                };

                $scope.clearSearchOSImageModal3_Custom = function(){
                    $scope.search_packages.name = null;
                    //The following is already being handled by the search_packages watcher.
                    //var container = document.getElementById('my_scroll_bar');
                    //container.scrollTop = 0;
                };

                $scope.closeOSImageModal3_Custom = function(){
                    modalInstance.dismiss('cancel');
                };

                $scope.backOSImageModal3_Custom = function(){
                    $scope.openOSImageModal3(_update_params.usb_device, true);
                    modalInstance.dismiss('cancel');
                };

                $scope.continueOSImage3_Custom = function(){
                    // update _update_params.packages_removed and harden_type
                    _update_params.harden_type = 'custom';
                    // gety array of objects
                    var temp_removed_packages = $scope.custom_installed_list.filter(function (el) {
                        return el.remove === true;
                    });
                    // get array:  this map only works starting from IE 9 (2011).
                    _update_params.packages_removed = temp_removed_packages.map(function(a) {return a.name;});

                    // update _data
                    _data.config_data = $scope.custom_configuration_data;
                    _data.install_list = $scope.custom_installed_list;
                    _data.updaters_list = null;

                    $scope.openOSImageModal3_Custom_MEC(_update_params, _data);
                    modalInstance.dismiss('cancel');
                };
            }

        });
    };

    $scope.openOSImageModal3_Custom_MEC = function(_update_params, _data){
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.osimage.3.custom.mec.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope){
                $scope.sortType = 'name';
                $scope.updater_path = null;
                $scope.checkingFile = false;
                $scope.save_image_has_error = false;
                $scope.save_image_error_message = '';

                // filter for ng-repeat to show not deleted entry
                $scope.search_not_deleted = {'delete': false};

                // if the _data.updaters_list is null, we create one from default configuration data.
                // else, we use the passed list
                if (!_data.updaters_list) {
                    var updaters_list = [];
                    if (typeof _data.config_data.mec_updater != 'undefined') {
                        updaters_list = _data.config_data.mec_updater;
                    }
                    $scope.custom_updaters_list = [];
                    for (i = 0; i < updaters_list.length; i++) {
                        $scope.custom_updaters_list.push({
                            'name': updaters_list[i],
                            'delete': false
                        });
                    }
                } else {
                    $scope.custom_updaters_list = _data.updaters_list;
                }

                $scope.addSearchOSImageModal3_Custom = function(){
                    $scope.checkingFile = true;

                    $scope.verifyFileExisted($scope.updater_path,
                        function successCallback(data){
                            if (data.status === 'success') {
                                $scope.checkingFile = false;
                                $scope.save_image_has_error = false;
                                $scope.save_image_error_message = '';
                                // add entry
                                // if the entry exists, update it.
                                var doesExist = false;
                                for (i = 0; i < $scope.custom_updaters_list.length; i++) {
                                    if ($scope.custom_updaters_list[i].name === $scope.updater_path) {
                                        if ($scope.custom_updaters_list[i].delete) {
                                            $scope.custom_updaters_list[i].delete = false;
                                        } else {
                                            $scope.showError('The file is already in the list.');
                                        }
                                        doesExist = true;
                                        break;
                                    }
                                }
                                if (!doesExist) {
                                    // if the entry does not exist, add it.
                                    $scope.custom_updaters_list.push({
                                        'name': $scope.updater_path,
                                        'delete': false
                                    });
                                }
                            } else {
                                 $scope.showError(data.message);
                            }
                        }, function errorCallback(response) {
                            $scope.showError('Failed to verify file! HTTP status is ' + response.status + '.');
                        });
                };

                $scope.showError = function(err_message){
                    $scope.checkingFile = false;
                    $scope.save_image_has_error = true;
                    $scope.save_image_error_message = err_message;

                    // add time out to clear error
                    setTimeout(function(){
                        $scope.save_image_has_error = false;
                        $scope.save_image_error_message = '';
                        $scope.$apply();
                    }, 2500);
                };

                $scope.clearSearchOSImageModal3_Custom = function(){
                    $scope.updater_path = null;
                };

                $scope.closeOSImageModal3_Custom = function(){
                    modalInstance.dismiss('cancel');
                };

                $scope.backOSImageModal3_Custom = function(){
                    $scope.openOSImageModal3_Custom_Packages(_update_params, _data);
                    modalInstance.dismiss('cancel');
                };

                $scope.continueOSImage3_Custom = function(){
                    // update _update_params.updaters and harden_type
                    _update_params.harden_type = 'custom';
                    // gety array of objects
                    var temp_updaters = $scope.custom_updaters_list.filter(function (el) {
                        return el.delete === false;
                    });
                    // get array:  this map only works starting from IE 9 (2011).
                    _update_params.updaters = temp_updaters.map(function(a) {return a.name;});

                    // update _data
                    _data.updaters_list = $scope.custom_updaters_list;
                    _data.users_list = null;

                    $scope.openOSImageModal3_Custom_User(_update_params, _data);

                    modalInstance.dismiss('cancel');
                };
            }

        });
    };

    $scope.openOSImageModal3_Custom_User = function(_update_params, _data){
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.osimage.3.custom.users.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope){
                $scope.sortType = 'sort_number';
                $scope.gettingAccount = false;
                $scope.save_image_has_error = false;
                $scope.save_image_error_message = '';
                $scope.custom_users_list = [];

                // create my array for users
                if (!_data.users_list) {
                    // if the list is null, we create our own
                    $scope.gettingAccount = true;
                    $scope.getAccounts(
                        function successCallback(data){
                            if (data.status === 'success') {
                                $scope.processData(data);
                            } else {
                                $scope.showError('Failed to get accounts list! Error is ' + data.message, false);
                            }
                        }, function errorCallback(response){
                            $scope.showError('Failed to get accounts list! HTTP status is ' + response.status, false);
                        });
                } else {
                    // not null, use the passed one.
                    $scope.custom_users_list = _data.users_list;
                }

                $scope.processData = function(data) {
                    $scope.gettingAccount = false;

                    // insert the MEC first
                    $scope.custom_users_list.push({
                        'name': 'McAfee Embedded Control',
                        'sort_number':  0,
                        'access': true,
                        'expanded': false,
                        'disable_toggle': true,
                        'new_password': '',
                        'confirm_password': ''
                    });

                    for (i = 0; i < data.accounts.length; i++) {
                        var sort_number = 4;
                        var allow_access = false;
                        if (data.accounts[i] === 'root') {
                            sort_number = 1;
                            allow_access = true;
                        } else if (data.accounts[i] === 'wra') {
                            sort_number = 2;
                            allow_access = true;
                        } else if (data.accounts[i] === 'gwuser') {
                            sort_number = 3;
                            allow_access = true;
                        } else {
                            sort_number = 4;
                            allow_access = false;
                        }
                        $scope.custom_users_list.push({
                            'name': data.accounts[i],
                            'sort_number':  sort_number,
                            'access': allow_access,
                            'expanded': false,
                            'disable_toggle': false,
                            'new_password': '',
                            'confirm_password': ''
                        });
                    }
                };

                $scope.showError = function(err_message, do_time_out) {
                    $scope.gettingAccount = false;
                    $scope.save_image_has_error = true;
                    $scope.save_image_error_message = err_message;

                    if (do_time_out) {
                        // add time out to clear error
                        setTimeout(function(){
                            $scope.save_image_has_error = false;
                            $scope.save_image_error_message = '';
                            $scope.$apply();
                        }, 2500);
                    }
                };

                $scope.closeOSImageModal3_Custom = function(){
                    modalInstance.dismiss('cancel');
                };

                $scope.backOSImageModal3_Custom = function(){
                    $scope.openOSImageModal3_Custom_MEC(_update_params, _data);
                    modalInstance.dismiss('cancel');
                };

                $scope.continueOSImage3_Custom = function(){
                    // verify password first
                    var hasError = false;
                    var checkErrorMessage = '';
                    for (i = 0; i < $scope.custom_users_list.length; i++) {
                        if ($scope.custom_users_list[i].access) {
                            if(!$scope.custom_users_list[i].new_password || $scope.custom_users_list[i].new_password.length < min_password_length){
                                checkErrorMessage = "For account " + $scope.custom_users_list[i].name + ", the password must be at least "+min_password_length+" characters long.";
                                hasError = true;
                                break;
                            }
                            if($scope.custom_users_list[i].new_password !==  $scope.custom_users_list[i].confirm_password){
                                checkErrorMessage = "For account " + $scope.custom_users_list[i].name + ", the passwords do not match.";
                                hasError = true;
                                break;
                            }
                        }
                    }
                    if (hasError) {
                        $scope.showError(checkErrorMessage, true);
                    } else {
                        // update _update_params.users and harden_type
                        _update_params.harden_type = 'custom';

                        // get array of objects
                        var temp_users = $scope.custom_users_list.filter(function (el) {
                            return el.access === true;
                        });
                        // get array:  this map only works starting from IE 9 (2011).
                        _update_params.users = temp_users.map(function(a) {return {'name': a.name, 'pw': a.confirm_password};});

                        // update _data
                        _data.users_list = $scope.custom_users_list;

                        // STIG is diabled for now
                        $scope.openOSImageModal4(_update_params);
                        //$scope.openOSImageModal3_Custom_stig(_update_params, _data);

                        modalInstance.dismiss('cancel');
                    }
                };
            }

        });
    };

    $scope.openOSImageModal3_Custom_stig = function(_update_params, _data){
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.osimage.3.custom.stig.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope){
                $scope.sortType = 'ID';
                $scope.custom_stig_list = [];

                // create my array for stig
                if (!_data.stig_list) {
                    if ("stig_all" in _data.config_data) {
                         // jQuery map to change JSON object to array of JSON
                        var temp_array = $.map(_data.config_data.stig_all, function(el, k) {
                            var el_return = el;
                            el_return.ID = k;
                            return el_return;
                        });

                        // if the list is null, we create our own
                        for (i = 0; i < temp_array.length; i++) {
                            var toInclude = true;

                            // check if this item is to be included by default
                            if ("stig" in _data.config_data) {
                                try {
                                    // works starting from IE 9.0
                                    if (_data.config_data.stig.indexOf(temp_array[i].ID) != -1) {
                                        toInclude = true;
                                    } else {
                                        toInclude = false;
                                    }
                                } catch(err) {
                                }
                            }

                            $scope.custom_stig_list.push({
                                'ID': temp_array[i].ID,
                                'Severity': temp_array[i].Severity,
                                'Title': temp_array[i].Title,
                                'URL': temp_array[i].URL,
                                'included': toInclude
                            });
                        }
                    }
                } else {
                    // not null, use the passed one.
                    $scope.custom_stig_list = _data.stig_list;
                }

                $scope.closeOSImageModal3_Custom = function(){
                    modalInstance.dismiss('cancel');
                };

                $scope.backOSImageModal3_Custom = function(){
                    $scope.openOSImageModal3_Custom_User(_update_params, _data);
                    modalInstance.dismiss('cancel');
                };

                $scope.continueOSImage3_Custom = function(){
                    // update _update_params.stig and harden_type
                    _update_params.harden_type = 'custom';

                    // get array of objects
                    var temp_stig = $scope.custom_stig_list.filter(function (el) {
                        return el.included === true;
                    });
                    // get array:  this map only works starting from IE 9 (2011).
                    _update_params.stig = temp_stig.map(function(a) {return a.ID;});

                    $scope.openOSImageModal4(_update_params);
                    modalInstance.dismiss('cancel');

                };
            }

        });
    };

    $scope.openOSImageModal4 = function(_update_params){
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.osimage.4.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope){

                $scope.closeOSImageModal4 = function(){
                    console.log("close os image modal 4");
                    modalInstance.dismiss('cancel');
                };

                $scope.continueOSImage5 = function(){
                    $scope.openOSImageModal5(_update_params);
                    modalInstance.dismiss('cancel');
                };
            }
        });
    };

    $scope.openOSImageModal5 = function(_update_params){
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.osimage.5.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope){

                $scope.closeSaveImage = function(){
                    console.log("closing window saving of os image");
                    modalInstance.dismiss('cancel');
                };

                $scope.savedImage = false;

                $scope.saveImage = function(){
                    console.log("hub sending saving os image with data "+ JSON.stringify(_update_params));

                    tempURL = '/api/sa' ;
                    content_body = {harden_type: _update_params.harden_type,
                                    usb_device: _update_params.usb_device.Device,
                                    packages_removed: _update_params.packages_removed,
                                    updaters: _update_params.updaters,
                                    users: _update_params.users,
                                    stig: _update_params.stig,
                                    admin_password: _update_params.new_admin_password,
                                    mec_password: _update_params.new_mec_admin_password
                                    };

                    workerProcessRestService.performGeneralExecution('POST', tempURL, false, true, content_body,
                        function successCallback(data) {
                            console.log("SAVED OS IMAGE");
                            $scope.savedImage = true;
                            $scope.openOSImageModal6();
                            modalInstance.dismiss('cancel');
                        }, function errorCallback(errorMessage) {
                            console.log("SAVE OS IMAGE FAILED");
                            $scope.openOSImageModal6e(errorMessage);
                            modalInstance.dismiss('cancel');
                        }, rest_check_interval_milliseconds_ll);
                };

                $scope.continueOSImage6 = function(){

                    $scope.openOSImageModal6();
                    modalInstance.dismiss('cancel');

                };
                $scope.saveImage();
            }
        });
    };

    $scope.openOSImageModal6 = function(){
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.osimage.6.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope){

                $scope.closeModal = function(){
                    console.log("closing window");
                    modalInstance.dismiss('cancel');
                };

                $scope.continueOSImage7 = function(){
                    $scope.openOSImageModal7();
                    modalInstance.dismiss('cancel');
                };
            }
        });
    };

    $scope.openOSImageModal6e = function(_message){
        var modalInstance = $uibModal.open({
            templateUrl: 'partials/hub.osimage.6e.ng.html?version=' + gui_version,
            animation:    true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function($scope,$state,$http,$rootScope){

                $scope.message    = _message;
                $scope.closeModal = function(){
                    console.log("closing window");
                    modalInstance.dismiss('cancel');
                };

            }

        });
    };

    $scope.openOSImageModal7 = function(){
        console.log("Is this Quark? " + $scope.isQuark);
        var html_page = 'partials/hub.osimage.7.ng.html?version=' + gui_version;
        if ($scope.isQuark) {
            html_page = 'partials/hub.osimage.7.quark.ng.html?version=' + gui_version;
        }

        var modalInstance = $uibModal.open({
            templateUrl: html_page,
            animation: true,
            size: 'lg',
            backdrop: modal_light_dismiss_enabled,
            scope: $scope,
            controller: function ($scope, $state, $http, $rootScope) {

                $scope.closeModal = function () {
                    console.log("closing window");
                    modalInstance.dismiss('cancel');
                };
                $scope.openSaveDeployTutorial = function () {
                    var url = $state.href('tutorial.savedeploy', { parameter: "parameter" });
                    window.open(url, '_blank');
                    console.log("openSaveDeployTutorial");
                };
                $scope.openDocumentation = function () {
                    modalInstance.dismiss('cancel');
                    $state.go("hub.dashboard.documentation");
                    console.log("openDocumentation");
                };
            }
        });
    };

};
