function confirmRestartOS($scope,$uibModal){
    var modalInstance = $uibModal.open({
        templateUrl: 'partials/hub.confirm.restart.os.ng.html?version=' + gui_version,
        animation:    true,
        size: 'lg',
        backdrop: modal_light_dismiss_enabled,
        scope: $scope,
        controller: function($scope,$state,$http,$rootScope,$timeout){
            $scope.restarting_os     = false;
            $scope.restarted_os      = false;
            $scope.message           = '';
            $scope.error_message     = '';

            $scope.restartOS = function(){
                if($scope.restarting_os){
                    return;
                }
                $scope.error_message = '';
                $scope.restarting_os = true;
                $scope.restarted_os  = false;
                $scope.rebootGateway(function successCallback(response) {
                    //no defined return json for this. if 200 OK then assume success of call (according to Nick)
                    console.log("reboot gateway process was successful  #1871b");
                    $scope.restarting_os = false;
                    $scope.restarted_os  = true;
                    $scope.message       = "The reboot process was successful. Forwarding to login in 10 seconds...";
                },function errorCallback(response) {
                    $scope.error_message = 'Rebooting gateway failed! Please manually reboot the gateway!';
                    console.log("reboot gateway  process failed  #1871E");
                }, restart_welcome_latency_milliseconds);
            };

            $scope.closeModal = function(){
                console.log("close confirm restart modal");
                modalInstance.dismiss('cancel');
            };
        }
    });
}