iotgatewayhub.config(function($stateProvider,$urlRouterProvider){
    
    $stateProvider.state('hub.networkconnected',{ 
        url:         '/hub/networkconnected', 
        templateUrl: '/partials/hub.networkconnected.ng.html?version=' + gui_version,
        controller: function($scope,$state,$http,$rootScope){
            if(!authorization.username){
                $state.go('welcome.login',{});
            }

            $scope.dashboard = function(){
                if ($scope.connected){
                    if ($scope.device.documentation_config == 'enabled'){
                        $scope.initGetTutorials('True');
                    }
                    if ($scope.device.softsoc_config == 'enabled'){
                        $scope.initGetFPGA('True');
                    }

                    $scope.initHubTasks('True');
                }
                else{
                    if ($scope.device.documentation_config == 'enabled'){
                        $scope.initGetTutorials('False');
                    }
                    if ($scope.device.softsoc_config == 'enabled'){
                        $scope.initGetFPGA('False');
                    }

                    $scope.initHubTasks('False');
                }
                $state.go('hub.dashboard.' + $scope.device.default_tab,{});
            };
        }
    });
});