iotgatewayhub.config(function($stateProvider){
    $stateProvider.state('hub.dashboard.tools',{
        url: '/dashboard/tools',
        templateUrl: '/partials/hub.dashboard.admin.ng.html?version=' + gui_version,
        controller: function($scope,$http,$uibModal){

            $http.get('api/tutorials?conf=admin').success(function(response) {
                if (response.result_code === 0) {
                    $scope.navOptions = response.data;
                } else {
                    //TODO:  Show Error!
                }
            });

            $scope.extensions = [];
            $scope.search = {};
            $scope.search.input = '';
            $scope.search.results = [];

            $scope.showSection = function (whichButton) {
                if (!whichButton) {
                    return true;
                }

                $scope.showSettings();

                $( ".admin-section-option-selected" ).each(function( index ) {
                    $(this).removeClass( "admin-section-option-selected" );
                });

                $("#" + whichButton.id).addClass( "admin-section-option-selected" );

                for (var navOptionId in $scope.navOptions) {
                    navOption = $scope.navOptions[navOptionId];
                    if (navOption.id === whichButton.id) {
                        $scope.selectedView = whichButton;
                        return true;
                    }
                }

                return false;
            };

            $scope.showSettings = function(option) {
                if (option) {
                    if (option.displayType === 'popup') {
                        if (option) {
                            $scope.dynamicChangeViewPartial = option.htmlLink;
                            window[option.javascriptFunction]($scope,$uibModal);
                        } else {
                            $scope.dynamicChangeViewPartial = null;
                        }
                    } else {
                        $scope.selectedOption = option;

                        if (option && option.htmlLink) {
                            $scope.dynamicChangeViewPartial = option.htmlLink;
                            window[option.javascriptFunction]($scope);
                        } else {
                            $scope.dynamicChangeViewPartial = null;
                        }
                    }
                } else {
                    $scope.selectedOption = null;
                }
            };

            $scope.shiftView = function(revert) {
                return $scope.selectedView && (revert ? $scope.selectedOption : !$scope.selectedOption);
            };

            $scope.searchOption = function(option) {
                if($scope.search.input.toLowerCase() && option.name.toLowerCase().indexOf($scope.search.input.toLowerCase()) > -1) {
                    return "admin-search-selection-right-arrow";
                }

                return "";
            };

            function updateSearchResults() {
                $scope.search.results.splice(0, $scope.search.results.length);

                if (!$scope.search.input) {
                    return;
                }

                var searchTerm = $scope.search.input.toLowerCase();
                var searchResults = new Map();

                for (var idx in $scope.navOptions) {
                    var section = $scope.navOptions[idx].name;
                    var groups = $scope.navOptions[idx].groups;

                    for (var jdx in groups) {
                        var options = groups[jdx].options;

                        for (var kdx in options) {
                            if (options[kdx].name.toLowerCase().indexOf(searchTerm) > -1) {
                                searchResults.set(section,$scope.navOptions[idx]);
                            }
                        }
                    }
                }

                if (searchResults.size > 0) {
                    searchResults.forEach(function(value) {
                        $scope.search.results.push(value);
                    });
                } else {
                    $scope.search.results = [{name:"No Search Results"}];
                }
            }

            $scope.$watch('search.input', function() {updateSearchResults();}, true);
        }
    });
});