// Used in HTML as hub-network-settings
iotgatewayhub.directive('hubNetworkSettings',function(){
    return {
        url:         '/hub/networksettings', 
        templateUrl: '/partials/hub.networksettings.ng.html?version=' + gui_version,
        resolve:     {},
        controller: function($scope,$state,$http,$rootScope,$timeout,workerProcessRestService){

            $scope.tmpSettings = {
                single_url: $scope.settings.proxy.single_url,
                single_port: $scope.settings.proxy.single_port,

                http_url: $scope.settings.proxy.http_url,
                http_port: $scope.settings.proxy.http_port,

                https_url: $scope.settings.proxy.https_url,
                https_port: $scope.settings.proxy.https_port,

                ftp_url: $scope.settings.proxy.ftp_url,
                ftp_port: $scope.settings.proxy.ftp_port,

                socks_url: $scope.settings.proxy.socks_url,
                socks_port: $scope.settings.proxy.socks_port,

                no_proxy: $scope.settings.proxy.no_proxy
            };

            function init(){
                if(!authorization.username){
                   $state.go('welcome.login',{});
                }

                var defaults ={
                    saveText: "Ok",
                    cancelText: "Cancel",
                    insideModal:false,
                    hideTestSettings:true,
                    maintainContextAfterSave: false
                };

                if ($scope.cancelText) defaults.cancelText = $scope.cancelText;

                // JQuery's extend to add more properties to $scope object
                $.extend($scope, defaults,{
                    isSaving: false,
                    barMessage: 'Saving network settings and adding Wind River repositories if needed. This may take a few minutes.',
                    isUpdatingChannelsFailed: false,
                    showSaveResult: false,
                    networkSettingsTest: {
                        running:false,
                        status:"Test settings",
                        result:"",
                        success:null,
                        isVisible:false
                    },
                    saveResult:{
                        message:"",
                        success:null,
                        isVisible:false
                    }
                });
            }

            init();

            $scope.onSaveSuccess = function(result){
                $scope.isSaving = false;
                $scope.saveResult.isVisible = true;
                $scope.saveResult.success = true;
                $scope.saveResult.message = "Saved settings";
                $timeout(function(){
                    $scope.saveResult.isVisible = false;
                }, linger_info_timeout_milliseconds);
            };

            $scope.onSaveFail = function(result){
                $scope.$emit("hub.unlockModal");

                $scope.isSaving = false;
                $scope.saveResult.isVisible = true;
                $scope.saveResult.success = false;
                $scope.saveResult.message = result.message;
                $timeout(function(){
                    $scope.saveResult.isVisible = false;
                }, linger_info_timeout_milliseconds);
            };

            $scope.nextAction = function(){
                var blockUI = true;

                $scope.$emit("hub.lockModal", [$scope]);

                $scope.isSaving =true;

                // should ideally be passed to applyProxySettings as a model
                $scope.settings.proxy = $scope.tmpSettings;

                function successApplyProxySettings(response) {
                    // update repos/channels
                    $scope.barMessage = 'Refreshing the installed package list. This may take a few minutes.';
                    tempURL = '/api/repository';
                    workerProcessRestService.performGeneralExecution('PUT', tempURL, true, false, null,
                        function successCallback(data) {
                            try {
                                var p_list = JSON.parse(data.p_list);
                                if ($scope.connected){
                                    $scope.setPackages('True', p_list);
                                }
                                else{
                                    $scope.setPackages('False', p_list);
                                }
                            } catch(err) {
                                // ignore error
                                console.log(err.message);
                            }

                            $scope.$emit("hub.unlockModal");

                            if ($scope.maintainContextAfterSave) {
                                $scope.onSaveSuccess(response);
                            } else {
                                $scope.doneSuccessCallback(response);
                            }

                        }, function errorCallback(errorMessage) {
                            $scope.$emit("hub.unlockModal");

                            $scope.barMessage = errorMessage;
                            $scope.isUpdatingChannelsFailed = true;
                        }, rest_check_interval_milliseconds_m);
                }

                $scope.applyProxySettings(successApplyProxySettings,  $scope.onSaveFail);
            };

            //$scope.networkSettingsTest = {running:true,status:'fail'};

            $scope.testProxySettingsCallback = function(result){
                $scope.networkSettingsTest.result = result ? "Connection established!": "Connection failed.";
                $scope.networkSettingsTest.success = !!result;

                $scope.networkSettingsTest.running=false;

                $timeout(function(){
                     $scope.networkSettingsTest.isVisible = false;
                    $scope.networkSettingsTest.success =false;
                },linger_info_timeout_milliseconds);
            };

            $scope.testSettings = function(){
                $scope.networkSettingsTest.isVisible = true;
                $scope.networkSettingsTest.running=true;
                $scope.networkSettingsTest.result="Testing..";
                $scope.testProxySettings(
                    function(){$scope.testProxySettingsCallback(true);},
                    function(){$scope.testProxySettingsCallback(false);});
            };
        }
    };
});