iotgatewayhub.config(function($stateProvider,$urlRouterProvider){
    $stateProvider.state('hub.dashboard.softsoc',{
        url: '/dashboard/softsoc',
        templateUrl: '/partials/hub.dashboard.softsoc.ng.html?version=' + gui_version,
        controller: function($window,$scope,$state,$rootScope,$timeout,$uibModal,utilityFunctionsService){
            $scope.sortType = 'parentSensorTitle';

            $scope.fpgaBuildAndReboot = function(){
                console.log("hub entering FPGA reboot");
                var modalInstance = $uibModal.open({
                templateUrl: 'partials/hub.confirm.fpga.reboot.ng.html?version=' + gui_version,
                animation:    true,
                size: 'lg',
                backdrop: modal_light_dismiss_enabled,
                scope: $scope,
                controller: function($scope,$state,$http,$rootScope){

                    $scope.cancelFPGA = function(){
                        console.log("hub canceling FPGA build and reboot");
                        $scope.fpga_num_comp = 0;
                        modalInstance.dismiss('cancel');
                    };

                    $scope.continueFPGA = function(){
                        $scope.showFpgaBuild();
                        modalInstance.dismiss('cancel');

                    };

                    }
                });
            };

            $scope.fpgaResetAndReboot = function(){
                console.log("hub entering FPGA reset and reboot");
                var modalInstance = $uibModal.open({
                templateUrl: 'partials/hub.confirm.fpga.reset.ng.html?version=' + gui_version,
                animation:    true,
                size: 'lg',
                backdrop: modal_light_dismiss_enabled,
                scope: $scope,
                controller: function($scope,$state,$http,$rootScope){

                    $scope.cancelFPGA = function(){
                        console.log("hub canceling FPGA reset and reboot");
                        $scope.fpga_num_comp = 0;
                        modalInstance.dismiss('cancel');
                    };

                    $scope.continueFPGA = function(){
                        console.log("hub in FPGA reset and reboot");
                        console.log("Uninstalling: " + $scope.installedBitstream.name);
                        $scope.uninstallPackage($scope.installedBitstream);
                        $scope.showFpgaReboot();
                        $scope.rebootGateway(
                                    function successCallback(response) {
                                    $scope.fpga_num_comp = 0;
                                    console.log("rebooted gateway: " + response.data.status);
                                    $timeout(function(){
                                        modalInstance.dismiss('cancel');
                                    }, restart_welcome_latency_milliseconds - 1000);
                                    }, function errorCallback(response) {
                                    console.log("gateway reboot failed");
                                    $scope.bar_after_message = "Rebooting gateway failed! Please manually restart the gateway!";
                                    }, restart_welcome_latency_milliseconds);
                        modalInstance.dismiss('cancel');

                    };

                    }
                });
            };

            $scope.setFPGAOption = function(option,enabled){
                for(var i = 0; i < $scope.fpga_num_comp.length; i++){
                    if (option == $scope.fpga_options[i]){
                        if (enabled === true){
                            $scope.fpga_num_comp[i] = 1;
                        }
                        else{
                            $scope.fpga_num_comp[i] = 0;
                        }
                    }
                }
            };

            $scope.fpgaValidateInput = function(){
                //calculate what options were selected through the UI
                for(var i = 0,gui_num_comp = 0; i < $scope.fpga_num_comp.length; i++){
                    if($scope.fpga_num_comp[i]){
                        gui_num_comp += Math.pow(2,i);
                    }
                }

                //compare UI selection with valid bitstream options
                for(i = 0; i < $scope.fpga_valid_num.length; i++){
                    if (gui_num_comp == $scope.fpga_valid_num[i]){
                        //save position in array that contains information on what package to install
                        $scope.fpga_package_install_position = i;
                        return true;
                    }
                }
                return false;
            };

            $scope.showFpgaBuild = function(){
                 var modalInstance = $uibModal.open({
                 templateUrl: 'partials/hub.inform.fpga.build.inprogress.ng.html?version=' + gui_version,
                 animation:    true,
                 size: 'lg',
                 backdrop: modal_light_dismiss_enabled,
                 scope: $scope,
                     controller: function($scope,$state,$http,$rootScope,$timeout){
                         $scope.bar_before_message = "Building FPGA image";
                         $scope.bar_after_message = "Building the FPGA image. Please wait...";
                         $scope.error_message = null;
                         $scope.show_close_button = false;

                         console.log('In build modal!');

                         $scope.installPackage($scope.fpga_options_data[$scope.fpga_package_install_position],
                            function successCallback(response) {
                                $scope.fpga_num_comp = 0;
                                $scope.showFpgaReboot();
                                $scope.rebootGateway(
                                    function successCallback(response) {
                                    console.log("rebooted gateway: " + response.data.status);
                                    $timeout(function(){
                                        modalInstance.dismiss('cancel');
                                    }, restart_welcome_latency_milliseconds - 1000);
                                    }, function errorCallback(response) {
                                    console.log("gateway reboot failed");
                                    $scope.bar_after_message = "Rebooting gateway failed! Please manually restart the gateway!";
                                    }, restart_welcome_latency_milliseconds);

                            $scope.message       = "The install was successful. Forwarding to login in 10 seconds...";
                            $timeout(function(){
                                        modalInstance.dismiss('cancel');
                                    }, restart_welcome_latency_milliseconds - 1000);

                            },
                            function errorCallback(response){
                                console.log("error installing fpga package");
                                modalInstance.dismiss('cancel');
                            },true);

                         $scope.closeModal = function(){
                             modalInstance.dismiss('cancel');
                         };
                     }
                  });
             };

             $scope.showFpgaReboot = function(){
                 var modalInstance = $uibModal.open({
                 templateUrl: 'partials/hub.inform.fpga.reboot.inprogress.ng.html?version=' + gui_version,
                 animation:    true,
                 size: 'lg',
                 backdrop: modal_light_dismiss_enabled,
                 scope: $scope,
                     controller: function($scope,$state,$http,$rootScope,$timeout){
                         $scope.bar_before_message = "System Reboot";
                         $scope.bar_after_message = "Rebooting system. Please wait...";
                         $scope.error_message = null;
                         $scope.show_close_button = false;

                         console.log('In reboot modal!');
                         $scope.closeModal = function(){
                             modalInstance.dismiss('cancel');
                         };
                     }
                  });
             };

            /*
            This function is called in an ng-repeat block to determine which package is installed
            and return what IP blocks will get installed from the package to display the checkmarks in the UI.
            */
            $scope.isFPGAImageInstalled = function(option){
                for(i = 0; i < $scope.fpga_options_data.length; i++){
                    if ($scope.fpga_options_data[i].installed === true){
                        $scope.installedBitstream = $scope.fpga_options_data[i];
                        if($scope.fpga_options_data[i][option] > 0){
                            return true;
                        }
                    }
                }
                return false;
            };

            $scope.isBitstreamInstalled = function(){
                for(i = 0; i < $scope.fpga_options_data.length; i++){
                    if ($scope.fpga_options_data[i].installed === true){
                        $scope.installedBitstream = $scope.fpga_options_data[i];
                        return true;
                    }
                }
                return false;
            };

        }
    });
});